import {
	mapState,
	mapMutations
} from 'vuex';
export default {
	data() {
		return {
			password: '',
			newPassword: '',
			reNewPassword: ''
		};
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		//修改密码
		save() {
			var isFormValid = true;
			if (!this.$api.util.validatePassword(this.password)) {
				this.$api.msg('密码为8-20位字母数字下划线组合');
				isFormValid = false;
			} else if (!this.$api.util.validatePassword(this.newPassword)) {
				this.$api.msg('密码为8-20位字母数字下划线组合');
				isFormValid = false;
			}else if (this.newPassword != this.reNewPassword) {
				this.$api.msg('两次新密码密码输入不一致');
				isFormValid = false;
			}
			if (!isFormValid) {
				return;
			}
			this.$api.request.changePassword({
				userUuid: this.userInfo.userUuid,
				password: this.password,
				newPassword: this.newPassword
			}, res => {
				if (res.body.status.statusCode === '0') {
					this.$api.msg('密码修改成功');
					setTimeout(function() {
						uni.switchTab({
							url: '/pages/user/user'
						})
					}, 1000);
				} else {
					this.$api.msg(res.body.status.errorDesc);
				}
			});
		},

	},
	mounted() {
		
	}
}
